.header {
    img {
        width: 100%;
        height: auto;
        border-radius: 10px;
        position: fixed;
        z-index: -1;
    }
    div {
        background: transparent;
    }
    .bubbles {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: -1;
        overflow: hidden;
        top: 0;
        left: 0;
    }
    .bubble {
        position: absolute;
        bottom: -100px;
        width: 40px;
        height: 40px;
        animation: rise 10s infinite ease-in;
    }
    .bubble:nth-child(1) {
        width: 40px;
        height: 40px;
        left: 10%;
        animation-duration: 8s;
    }
    .bubble:nth-child(2) {
        width: 20px;
        height: 20px;
        left: 20%;
        animation-duration: 5s;
        animation-delay: 1s;
    }
    .bubble:nth-child(3) {
        width: 50px;
        height: 50px;
        left: 35%;
        animation-duration: 7s;
        animation-delay: 2s;
    }
    .bubble:nth-child(4) {
        width: 80px;
        height: 80px;
        left: 50%;
        animation-duration: 11s;
        animation-delay: 0s;
    }
    .bubble:nth-child(5) {
        width: 35px;
        height: 35px;
        left: 55%;
        animation-duration: 6s;
        animation-delay: 1s;
    }
    .bubble:nth-child(6) {
        width: 45px;
        height: 45px;
        left: 65%;
        animation-duration: 8s;
        animation-delay: 3s;
    }
    .bubble:nth-child(7) {
        width: 90px;
        height: 90px;
        left: 70%;
        animation-duration: 12s;
        animation-delay: 2s;
    }
    .bubble:nth-child(8) {
        width: 25px;
        height: 25px;
        left: 80%;
        animation-duration: 6s;
        animation-delay: 2s;
    }
    .bubble:nth-child(9) {
        width: 15px;
        height: 15px;
        left: 70%;
        animation-duration: 5s;
        animation-delay: 1s;
    }
    .bubble:nth-child(10) {
        width: 90px;
        height: 90px;
        left: 25%;
        animation-duration: 10s;
        animation-delay: 5s;
    }
    .bubble:nth-child(11) {
        width: 40px;
        height: 40px;
        left: 15%;
        animation-duration: 8s;
    }
    .bubble:nth-child(12) {
        width: 20px;
        height: 20px;
        left: 25%;
        animation-duration: 5s;
        animation-delay: 2s;
    }
    .bubble:nth-child(13) {
        width: 50px;
        height: 50px;
        left: 30%;
        animation-duration: 7s;
        animation-delay: 3s;
    }
    .bubble:nth-child(14) {
        width: 80px;
        height: 80px;
        left: 40%;
        animation-duration: 11s;
        animation-delay: 1s;
    }
    .bubble:nth-child(15) {
        width: 35px;
        height: 35px;
        left: 50%;
        animation-duration: 6s;
        animation-delay: 2s;
    }
    .bubble:nth-child(16) {
        width: 45px;
        height: 45px;
        left: 60%;
        animation-duration: 9s;
        animation-delay: 4s;
    }
    .bubble:nth-child(17) {
        width: 90px;
        height: 90px;
        left: 75%;
        animation-duration: 12s;
        animation-delay: 4s;
    }
    .bubble:nth-child(18) {
        width: 25px;
        height: 25px;
        left: 85%;
        animation-duration: 8s;
        animation-delay: 3s;
    }
    .bubble:nth-child(19) {
        width: 15px;
        height: 15px;
        left: 70%;
        animation-duration: 5s;
        animation-delay: 2s;
    }
    .bubble:nth-child(20) {
        width: 90px;
        height: 90px;
        left: 25%;
        animation-duration: 12s;
        animation-delay: 5s;
    }
    @keyframes rise {
        0% {
            bottom: -100px;
            transform: translateX(0);
        }
        50% {
            transform: translate(100px);
        }
        100% {
            bottom: 1080px;
            transform: translateX(-200px);
        }
    }
}
